.message {
    border: 1px solid red;
    text-align: right;
    margin-top: 1rem;
    

}

.imageAndText {
   text-align: right;
   
}

.imageAndText img {
    float: right;
}

.text {
    color: white;
    background-color: rgba(0, 102, 204, 255);
    display: inline-block;
    border-radius: 5px;
    max-width: 50%;
    margin-right: 2rem;
    padding: 1rem;
    position: relative;
    
}

.text::after {
    content: '';
        position: absolute;
        width: 0;
        height: 0;
        right: -20px;
        bottom: 0;
        border: 22px solid;
        border-color:  transparent transparent rgba(0, 102, 204, 255) transparent;
        z-index: -1;
}

