.friendImageAndText {
    /*background-color: rgba(245, 247, 251, 255);*/
    
    max-width: 50%;
}

.friendImageAndText img {
    float: left;
    margin-right: 2rem;
}

.friendText {
    display: inline-block;
    background-color: rgb(178, 185, 197);
    border-radius: 5px;
    padding: 1rem;
    position: relative;
}

.friendText:after {
content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    left: -20px;
    right: auto;
    top: 0;
    bottom: auto;
    border: 22px solid;
    border-color: rgb(178, 185, 197) transparent transparent transparent;
}

