.App {
    --header_height: 60px;
    width: 75%;
    margin: 0 auto;
    padding: 5rem;
}

.hw {
    width: 100%;
}

.hwTitle {
    border-bottom: 2px solid rgba(145, 138, 138, 0.265);
    padding-top: 3rem;
    padding-bottom: 1rem;
    font-size: 2rem;
    font-style: 900;
}

img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    object-fit: cover;
}

pre {
    overflow-x: auto;
    white-space: pre-wrap;
    word-wrap: break-word;
    font-size: 15px;

}